import { FunctionComponent } from 'preact';
import { classes, Slot } from '@adobe-commerce/elsie/lib';
import { memo } from 'preact/compat';
import {
  Field,
  Input,
  Picker,
  Checkbox,
  InputDate,
  TextArea,
} from '@adobe-commerce/elsie/components';
import { FieldEnumList } from '@/account/data/models';
import { FieldsProps, FormInputsProps } from '@/account/types';

export const FormInputs: FunctionComponent<FormInputsProps> = memo(
  ({
    loading,
    values,
    fields = [],
    errors,
    className = '',
    onChange,
    onBlur,
    onFocus,
    slots,
    fieldIdPrefix,
  }) => {
    const itemClassName = `${className}__field`;

    const renderSlot = (item: any, errorMessage: string) => {
      if (!slots?.[`AddressFormInput_${item.code}`]) return;

      const contextValue = {
        inputName: item.customUpperCode,
        handleOnChange: onChange,
        handleOnBlur: onBlur,
        handleOnFocus: onFocus,
        errorMessage,
        errors,
        config: item,
      };

      return (
        <Slot
          data-testid={`addressFormInput_${item.code}`}
          name={`AddressFormInput_${item.code}`}
          slot={slots[`AddressFormInput_${item.code}`]}
          context={contextValue}
          key={item.id}
        />
      );
    };

    const selectElement = (
      item: any,
      valueMessage: string,
      errorMessage: string
    ) => {
      const defaultSelectValue =
        item.options.find(
          (option: { isDefault: boolean; value: string; text: string }) =>
            option.isDefault
        )?.value ??
        valueMessage ??
        item.defaultValue;

      return (
        <>
          {renderSlot(item, errorMessage) ?? (
            <Field
              key={item.id}
              error={errorMessage}
              className={classes([
                itemClassName,
                `${itemClassName}--${item.id}`,
                [`${itemClassName}--${item.id}-hidden`, item.isHidden],
                item.className,
              ])}
              data-testid={`${className}--${item.id}`}
              disabled={loading || item.disabled}
            >
              <Picker
                aria-label={item.label}
                aria-required={item.required ? 'true' : 'false'}
                id={fieldIdPrefix ? `${fieldIdPrefix}_${item.code}` : item.code}
                required={item?.required}
                name={item.customUpperCode}
                floatingLabel={`${item.label} ${item.required ? '*' : ''}`}
                placeholder={item.label!}
                options={item.options}
                onBlur={onBlur}
                onFocus={onFocus}
                handleSelect={onChange}
                defaultValue={defaultSelectValue}
                value={defaultSelectValue}
              />
            </Field>
          )}
        </>
      );
    };

    const inputElement = (
      item: any,
      valueMessage: string,
      errorMessage: string
    ) => {
      return (
        <>
          {renderSlot(item, errorMessage) ?? (
            <Field
              key={item.id}
              error={errorMessage}
              className={classes([
                itemClassName,
                `${itemClassName}--${item.id}`,
                [`${itemClassName}--${item.id}-hidden`, item.isHidden],
                item.className,
              ])}
              data-testid={`${className}--${item.id}`}
              disabled={loading}
            >
              <Input
                aria-label={item.label}
                aria-required={item.required ? 'true' : 'false'}
                id={fieldIdPrefix ? `${fieldIdPrefix}_${item.code}` : item.code}
                type="text"
                name={item.customUpperCode}
                value={valueMessage ?? item.defaultValue}
                placeholder={item.label}
                floatingLabel={`${item.label} ${item.required ? '*' : ''}`}
                onBlur={onBlur}
                onFocus={onFocus}
                onChange={onChange}
              />
            </Field>
          )}
        </>
      );
    };

    const inputDateElement = (
      item: any,
      valueMessage: string,
      errorMessage: string
    ) => {
      return (
        <>
          {renderSlot(item, errorMessage) ?? (
            <Field
              key={item.id}
              error={errorMessage}
              className={classes([
                itemClassName,
                `${itemClassName}--${item.id}`,
                [`${itemClassName}--${item.id}-hidden`, item.isHidden],
                item.className,
              ])}
              data-testid={`${className}--${item.id}`}
              disabled={loading || item.disabled}
            >
              <InputDate
                aria-label={item.label}
                aria-required={item.required ? 'true' : 'false'}
                id={fieldIdPrefix ? `${fieldIdPrefix}_${item.code}` : item.code}
                type="text"
                name={item.customUpperCode}
                value={valueMessage || item.defaultValue}
                placeholder={item.label}
                floatingLabel={`${item.label} ${item.required ? '*' : ''}`}
                onBlur={onBlur}
                onChange={onChange}
                disabled={loading || item.disabled}
              />
            </Field>
          )}
        </>
      );
    };

    const inputCheckBoxElement = (
      item: any,
      valueMessage: string,
      errorMessage: string
    ) => {
      return (
        <>
          {renderSlot(item, errorMessage) ?? (
            <Field
              key={item.id}
              error={errorMessage}
              className={classes([
                itemClassName,
                `${itemClassName}--${item.id}`,
                [`${itemClassName}--${item.id}-hidden`, item.isHidden],
                item.className,
              ])}
              data-testid={`${className}--${item.id}`}
              disabled={item?.isHidden ?? loading}
            >
              <Checkbox
                aria-label={item.label}
                aria-required={item.required ? 'true' : 'false'}
                aria-hidden={item.isHidden}
                tabindex={item?.isHidden ? -1 : 0}
                id={fieldIdPrefix ? `${fieldIdPrefix}_${item.code}` : item.code}
                name={item.customUpperCode}
                checked={valueMessage || item.defaultValue}
                placeholder={item.label}
                label={`${item.label} ${item.required ? '*' : ''}`}
                onBlur={onBlur}
                onChange={onChange}
              />
            </Field>
          )}
        </>
      );
    };

    const textAreaElement = (
      item: any,
      valueMessage: string,
      errorMessage: string
    ) => {
      return (
        <>
          {renderSlot(item, errorMessage) ?? (
            <Field
              key={item.id}
              error={errorMessage}
              className={classes([
                itemClassName,
                `${itemClassName}--${item.id}`,
                [`${itemClassName}--${item.id}-hidden`, item.isHidden],
                item.className,
              ])}
              data-testid={`${className}--${item.id}`}
              disabled={loading}
            >
              <TextArea
                aria-label={item.label}
                aria-required={item.required ? 'true' : 'false'}
                id={fieldIdPrefix ? `${fieldIdPrefix}_${item.code}` : item.code}
                type="text"
                name={item.customUpperCode}
                value={valueMessage ?? item.defaultValue}
                label={`${item.label} ${item.required ? '*' : ''}`}
                onBlur={onBlur}
                onChange={onChange}
              />
            </Field>
          )}
        </>
      );
    };

    if (!fields.length) return null;

    return (
      <>
        {fields.map((item: FieldsProps) => {
          const errorMessage = errors?.[item.customUpperCode] as string;
          const valueMessage = values?.[item.customUpperCode] as string;

          switch (item.fieldType) {
            case FieldEnumList.TEXT: {
              if (item.options.length) {
                return selectElement(item, valueMessage, errorMessage);
              }

              return inputElement(item, valueMessage, errorMessage);
            }

            case FieldEnumList.MULTILINE: {
              return inputElement(item, valueMessage, errorMessage);
            }

            case FieldEnumList.SELECT: {
              return selectElement(item, valueMessage, errorMessage);
            }

            case FieldEnumList.DATE: {
              return inputDateElement(item, valueMessage, errorMessage);
            }

            case FieldEnumList.BOOLEAN: {
              return inputCheckBoxElement(item, valueMessage, errorMessage);
            }

            case FieldEnumList.TEXTAREA: {
              return textAreaElement(item, valueMessage, errorMessage);
            }

            default:
              return null;
          }
        })}
      </>
    );
  }
);
